import { useEffect, useState } from "react";
import { MapContainer, TileLayer, Marker } from 'react-leaflet';
import { LeafletTrackingMarker } from "react-leaflet-tracking-marker";
import L, { Popup } from "leaflet";

const icon = L.icon({
  iconSize: [45, 45],
  popupAnchor: [2, -20],
  iconUrl:
    "https://mysql-backup-base.s3.amazonaws.com/New%20Project%20%281%29.png"
});

export default function AirplaneMarker({ data }) {
  const { lat, lng } = data;
  const [prevPos, setPrevPos] = useState([lat, lng]);

  useEffect(() => {
    if (prevPos[1] !== lng && prevPos[0] !== lat) 
    setPrevPos([lat, lng]);
  }, [lat, lng, prevPos]);

  return (
    <>
    {/* <MapContainer center={position} zoom={13} scrollWheelZoom={true}>
              <LayersControl>
                <BaseLayer checked name="OpenStreetMap">
                  <TileLayer
                    url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                    attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                  />
                </BaseLayer>
                <BaseLayer name="Google-Street View">
                  <TileLayer
                    attribution="Google Maps"
                    url="https://www.google.cn/maps/vt?lyrs=m@189&gl=cn&x={x}&y={y}&z={z}"
                  />
                </BaseLayer>
                <BaseLayer checked name="Google-Satelite">
                  <TileLayer
                    url="https://{s}.google.com/vt/lyrs=s&x={x}&y={y}&z={z}"
                    maxZoom={20}
                    subdomains={["mt1", "mt2", "mt3"]}
                  />
                </BaseLayer>
            </LayersControl>
            
          </MapContainer> */}
          <LeafletTrackingMarker
        icon={icon}
        position={[lat, lng]}
        previousPosition={prevPos}
        duration={1000}
        keepAtCenter={true}
      ></LeafletTrackingMarker>
    </>
  );
}
